import React, { memo } from 'react'
import apexBackground from "../images/apexBackground.png"

const About = memo(() => {
    return (
        <>
            <div section className="section  is-primary has-text-centered is-long" id="about"
                style={{
                    // backgroundImage: `url(${apexBackground})`,
                    backgroundColor: "#312d2a"
                }}>
                <div className="container">
                    <div className="columns is-centered">
                        <div className="column is-three-fifths">
                            <h1 className="title is-spaced is-size-3-desktop is-size-4-mobile">About Myself</h1>
                            <h2 className="subtitle is-size-5-desktop has-text-weight-normal"> "I am a self-motivated, detail-oriented Oracle APEX developer with expertise in frontend development, JavaScript, and PL/SQL. Passionate about exploring new technologies and advancements in AI, I value dedication and hard work. Join me on a journey to experience cutting-edge technology and innovative solutions"</h2>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
})

export default About